import React, { useEffect, useState, useRef } from 'react';
import { GestureRecognizer, FilesetResolver, DrawingUtils } from "@mediapipe/tasks-vision";
import Navbar from '../../components/Navbar';

export default function Guide() {
  const [gestureRecognizer, setGestureRecognizer] = useState(null);
  const [runningMode, setRunningMode] = useState("IMAGE");
  const [webcamRunning, setWebcamRunning] = useState(false);
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const gestureOutputRef = useRef(null);
  const videoHeight = 360;
  const videoWidth = 480;
  let lastVideoTime = -1;
  let results = null;

  useEffect(() => {
    const initializeGestureRecognizer = async () => {
      try {
        const vision = await FilesetResolver.forVisionTasks(
          "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@0.10.3/wasm"
        );
        const recognizer = await GestureRecognizer.createFromOptions(vision, {
          baseOptions: {
            modelAssetPath: "../../model/point_history_classifier.tflite", // Укажите путь к вашей модели
            delegate: "GPU"
          },
          runningMode: runningMode
        });
        setGestureRecognizer(recognizer);
      } catch (error) {
        console.error("Failed to initialize gesture recognizer", error);
      }
    };

    initializeGestureRecognizer();
  }, [runningMode]);

  const handleImageClick = async (event) => {
    if (!gestureRecognizer) {
      alert("Please wait for gestureRecognizer to load");
      return;
    }

    if (runningMode === "VIDEO") {
      setRunningMode("IMAGE");
      await gestureRecognizer.setOptions({ runningMode: "IMAGE" });
    }

    const img = event.target;
    const results = await gestureRecognizer.recognize(img);

    console.log(results);

    if (results.gestures.length > 0) {
      const p = event.target.parentNode.childNodes[3];

      p.setAttribute("class", "info");

      const categoryName = results.gestures[0][0].categoryName;
      const categoryScore = parseFloat(results.gestures[0][0].score * 100).toFixed(2);
      const handedness = results.handednesses[0][0].displayName;

      p.innerText = `GestureRecognizer: ${categoryName}\n Confidence: ${categoryScore}%\n Handedness: ${handedness}`;
      p.style = `left: 0px; top: ${img.height}px; width: ${img.width - 10}px;`;

      const canvas = document.createElement("canvas");
      canvas.setAttribute("class", "canvas");
      canvas.setAttribute("width", img.naturalWidth + "px");
      canvas.setAttribute("height", img.naturalHeight + "px");
      canvas.style = `left: 0px; top: 0px; width: ${img.width}px; height: ${img.height}px;`;

      event.target.parentNode.appendChild(canvas);

      const canvasCtx = canvas.getContext("2d");
      const drawingUtils = new DrawingUtils(canvasCtx);

      for (const landmarks of results.landmarks) {
        drawingUtils.drawConnectors(
          landmarks,
          GestureRecognizer.HAND_CONNECTIONS,
          {
            color: "#00FF00",
            lineWidth: 5
          }
        );

        drawingUtils.drawLandmarks(landmarks, {
          color: "#FF0000",
          lineWidth: 1
        });
      }
    }
  };

  const enableWebcam = () => {
    if (!gestureRecognizer) {
      alert("Please wait for gestureRecognizer to load");
      return;
    }

    setWebcamRunning((prev) => !prev);
  };

  useEffect(() => {
    const video = videoRef.current;
    const canvasElement = canvasRef.current;
    const canvasCtx = canvasElement.getContext("2d");
    const gestureOutput = gestureOutputRef.current;

    const handleStream = (stream) => {
      video.srcObject = stream;
      video.addEventListener("loadeddata", predictWebcam);
    };

    const predictWebcam = async () => {
      if (runningMode === "IMAGE") {
        setRunningMode("VIDEO");
        await gestureRecognizer.setOptions({ runningMode: "VIDEO" });
      }

      if (video.videoWidth === 0 || video.videoHeight === 0) {
        requestAnimationFrame(predictWebcam);
        return;
      }

      let nowInMs = Date.now();

      if (video.currentTime !== lastVideoTime) {
        lastVideoTime = video.currentTime;
        results = await gestureRecognizer.recognizeForVideo(video, nowInMs);
      }

      if (!results || !results.landmarks || !results.gestures) {
        requestAnimationFrame(predictWebcam);
        return;
      }

      canvasCtx.save();
      canvasCtx.clearRect(0, 0, canvasElement.width, canvasElement.height);
      const drawingUtils = new DrawingUtils(canvasCtx);

      canvasElement.style.height = `${videoHeight}px`;
      video.style.height = `${videoHeight}px`;
      canvasElement.style.width = `${videoWidth}px`;
      video.style.width = `${videoWidth}px`;

      if (results.landmarks) {
        for (const landmarks of results.landmarks) {
          drawingUtils.drawConnectors(
            landmarks,
            GestureRecognizer.HAND_CONNECTIONS,
            {
              color: "#00FF00",
              lineWidth: 5
            }
          );

          drawingUtils.drawLandmarks(landmarks, {
            color: "#FF0000",
            lineWidth: 2
          });
        }
      }

      if (results.gestures.length > 0) {
        const categoryName = results.gestures[0][0].categoryName;
        const categoryScore = parseFloat(results.gestures[0][0].score * 100).toFixed(2);
        const handedness = results.handednesses[0][0].displayName;

        gestureOutput.innerText = `GestureRecognizer: ${categoryName}\n Confidence: ${categoryScore}%\n Handedness: ${handedness}`;
      }

      canvasCtx.restore();

      if (webcamRunning) {
        requestAnimationFrame(predictWebcam);
      }
    };

    if (webcamRunning) {
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        navigator.mediaDevices.getUserMedia({ video: true }).then(handleStream);
      }
    } else {
      video.pause();
      video.srcObject?.getTracks().forEach(track => track.stop());
      if (canvasCtx) {
        canvasCtx.clearRect(0, 0, canvasElement.width, canvasElement.height);
      }
    }

    return () => {
      if (video) {
        video.pause();
        video.srcObject?.getTracks().forEach(track => track.stop());
      }
    };
  }, [webcamRunning, gestureRecognizer, runningMode]);

  return (
    <div>
      <Navbar />
      <section className="guide">
        <div className="image-container">
          <div className="overlay-container">
            <img src="path/to/your/gesture_image.png" alt="Gesture" onClick={handleImageClick} />
            <p className="output"></p>
          </div>
        </div>
        <div className="webcam-container">
          <div className="overlay-container">
            <video ref={videoRef} autoPlay className="webcam" width={videoWidth} height={videoHeight} />
            <canvas ref={canvasRef} className="canvas" width={videoWidth} height={videoHeight} />
            <p ref={gestureOutputRef} className="output"></p>
          </div>
        </div>
        <button onClick={enableWebcam}>
          {webcamRunning ? "Stop Webcam" : "Start Webcam"}
        </button>
      </section>
    </div>
  );
}
